@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@700&family=Secular+One&display=swap');

.button {
  height: 5rem;
  width: 15rem;
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: row;
  border-radius: 0.5rem;
  border: 0;
  background: rgba(255, 255, 255, 0);

}

.tel {
  height: 2rem;
  width: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 0.5rem;
}

.link {
  text-decoration: none;
  color: var(--font-color1);
  display: flex;
  align-items: center;
  justify-content: center;
  font: 400 1.1rem "Secular One", sans-serif;

}

:root {
  --button-color: #257A48;
  --font-color1: rgb(0, 0, 0);
  --button-colort: #42e787;
}