@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@700&family=Secular+One&display=swap');


* {
  margin: 0;
  padding: 0;
}

#container {
  width: 100%;
}

.initial {
  display: flex;
  justify-content: space-between;
  height: 4rem;

  display: flex;
  align-items: center;
  padding: 1rem;
  background: var(--header-color);
}

.navigation {
  display: none;
}

.onest {
  margin: 4px var(--shadow-color);
  padding: 5rem 0;
}

.main {
  margin-top: 1.5rem;
  margin-left: 1.5rem;
  margin-right: 1.5rem;
  text-align: center;
}

.titulo {
  padding: 1.5rem;
  color: var(--button-color);
  font: 700 var(--font-size-fira) "Fira Sans", sans-serif;
}

.doctor1 {
  width: 17rem;
  height: 18rem;
  position: relative;
  border-radius: 0.5rem;
}


.imgbox::before {
  content: "";
  height: 18rem;
  width: 17rem;
  background: var(--shadow-color);
  position: absolute;
  top: 10rem;
  left: 0.5rem;
  border-radius: 0.5rem;
  z-index: 0;
}

.text {
  text-align: center;
  font: 400 var(--font-size-secular) "Secular One", sans-serif;
  text-align: center;
  padding-bottom: 2rem;

}

.bottom {
  width: 100%;
  height: 2px;
  background: var(--shadow-color);
}

.options {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 1rem 0;
  background: var(--container-color);
}





.footer {
  height: 20rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: var(--button-colort);
  padding-top: 2rem;
  font: 400 var(--font-size-secular) "Secular One", sans-serif;

}

.map {
  height: 30rem;
}

.link0 {
  color: black;
  text-decoration: none;
}


/* //////////MEDIA/////////// */

@media (min-width: 1000px) {
  .navigation {
    display: flex;
  }

  .nav {
    display: flex;
    margin-right: 1rem;
    text-decoration: none;
    font: 400 var(--font-size-secular-one) "Secular One", sans-serif;
    color: black;
  }

  .titulo {
    font: 700 var(--font-size-fira-sans) "Fira Sans", sans-serif;
    display: flex;
    text-align: center;

  }

  .text {
    font: 400 var(--font-size-secular-one) "Secular One", sans-serif;
    text-align: center;
  }

  .onest {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

  }

  .textaling {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .doctor1 {
    margin-left: 1rem;
  }

  .imgbox::before {
    margin-left: 1rem;

  }

  .doctor1,
  .desk .textaling {
    display: flex;
    justify-content: space-between;
  }

  .desk {
    display: flex;
    flex-direction: column;
    width: 40rem;
    text-align: center;
  }

  .footer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }

  .big_logo {
    display: flex;
    align-items: center;

  }

  .part_footer {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-right: 5rem;
  }

  :root {
    --font-size-secular-one: 1.25rem;
    --font-size-fira-sans: 2.25rem;
    --img: ;
  }
}


:root {
  --button-color: #257A48;
  --container-color: #96FBC0;
  --header-color: #4CFA94;
  --shadow-color: #497A5E;
  --button-colort: #42e787;
  --font-color1: #ffff;

  --font-size-secular: 1rem;
  --font-size-fira: 2rem;

}