@import url('https://fonts.googleapis.com/css2?family=Fira+Sans:wght@700&family=Secular+One&display=swap');

.accordion p {
  overflow: hidden;

  font: 400 1rem "Secular One", sans-serif;
  text-align: center;
  padding: 0.5rem;
  border-radius: 0.5rem;

}

.acc {
  margin: 2rem 0;

  height: 8rem;
  width: 8rem;
  border-radius: 2rem;
  border: 0;
  background: var(--button-colort);
}

.icons {
  height: 6rem;
  width: 6rem;

}

@keyframes move {
  0% {
    width: 0%;

  }

  100% {
    width: 100%;

  }
}